.dark-mode{
    background-color: darken($color: $grisOscuro, $amount: 10);

    p{
        color: $blanco;
    }
    a, h1, h2, h3, h4{
        color: $gris;
    }

    .anuncio, .resumen-propiedad{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;

        .precio{
            color: $verde;
        }
    }

    .icono-img{
        filter: invert(100%);
    }

    .texto-nosotros blockquote{
        color: $blanco;
    }

    .formulario{
        legend, p, label{
            color: $gris;
        }

        input:not([type='submit']), textarea, select{
            background-color: $grisOscuro;
            color: $blanco;
            border: 1px solid $grisOscuro;
        }
    }

    table{
        thead, tbody{
            color: $blanco;
        }
        tbody{
            background-color: $grisOscuro;
        }
    
    }
}
