@include tablet {
    .contenedor-anuncios {
        @include grid(3, 2rem); 
        margin: 0 auto;
        width: 100%;  
    }
}

.anuncio {
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris, 10);
    text-align: center;
    margin: 2rem 1rem;
    width: 100%;

    .contenido-anuncio {
        padding: 2rem;
    }

    h3,
    p {
        margin: 0;
    }


}

.precio {
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    justify-content: space-evenly;
    max-width: 40rem;

    li {
        display: flex;
        flex: 1;
    }

    img {
        flex: 0 0 5rem;
        margin-right: 1rem;
    }
    p{
        font-weight: $black;
    }
}